body {
	font-family: Helvetica, Arial, Verdana, sans-serif;
	font-size: 100%;
	/* background: url(graphics/14.gif); */
}

h1 {
	font-size: 2.5em;
	font-weight: bold;
	line-height: 1.255em;
	padding-bottom: .331em;
	/* 	padding-bottom: .94em; */
	padding-top: .31em;
}

h2 {
	font-weight: bold;
	font-size: 1.8em;
	line-height: 1.33em;
	padding-bottom: .06em;
}

h3 {
	font-weight: bold;
	font-size: 1.4em;
	line-height: 1.57em;
	padding-top: 1.57em;
}

h4,
h5,
h6 {
	font-size: 1em;
	line-height: 1.57em;
	padding-top: 1.57em;
	font-weight: bold;
}

dl,
ol,
p,
ul {
	font-size: 1em;
	line-height: 1.57em;
	padding-bottom: 1.2em;
	padding-top: .47em;
	/* 	padding-top: 1.97em; */
}
