body {
	$grey: #7F7F7F;
	$lightgrey: #B2B2B2;
	padding: 3em 1em 1em;

	a {
		color: $green;
		text-decoration: none;
		&:hover {
			color: $black;
		}
	}

	header {
		> div {
			padding-top: 2em;

			img {}

			ul {
				list-style: none;

				li {
					display: inline-block;
					text-align: center;
					text-transform: uppercase;
					font-size: 0.8em;
					letter-spacing: 0.25em;
					color: $lightgrey;

					span {
						display: none;
					}
				}
			}
			.address {
				padding: 1em 0;
				color: rgba(0,0,0,.3);
				letter-spacing: .06em;

				font: bold "Lucida Grande", sans-serif;
				text-transform: uppercase;


				text-align: center;
				br {
					display: none;
				}
				.zip {
					display: none;
				}
				.phone {
					font-weight: bold;
					display: block;
					letter-spacing: .3em;
				}
			}
		}

		nav {
			display: hidden;

			&:before {
				position: absolute;
				top: 0;
				content:"Menu";
				font-size: 1em;
				font-weight: 100;
				letter-spacing: 0.05em;
				text-transform: uppercase;
				text-align: center;
				color: $grey;
				width: 94%;
				left: 3%;
				display: block;
				padding: 1em 0 1em;
				border-bottom: 3px solid #eee;
			}

			ul {
				display: none;
				list-style: none;

				a {}
			}
		}
	}

	figure {
		background-image: url('images/storefront.png');
		background-size: 100%;
		margin-top: 2em;
		padding: 5em 0;
		h1 {
			color: white;
			font-weight: 100;
			font-size: 1.5em;
			text-align: center;
			text-transform: uppercase;
			text-shadow: 0 0 5px #333;
		}
	}

	.info {
		p {
			a {}
		}
	}

	.black {
		div {
			width: 49%;
			height: 200px;
			overflow: hidden;
			text-align: center;
			display: inline-block;

			img {
				width: 100%;
			}
		}
	}

	.basement {}
}
